import { request } from '../../request.js'
export default {
	data() {
		return {
			addressList: {},
			page:1,
		};
	},
	onShow() {
		this.getAddressList(this.page);
	},
	methods: {
		//获取数据
		getAddressList(page){
			let that = this;
			request({
				url: 'index.php?s=/wap/member/memberAddress',
			})
			.then(res => {
				console.log(res);
				let addressList = res.data.list.data;
				that.addressList = addressList;
			});
		},
		//设置默认收货
		setDefault(id){
			let that = this;
			request({
				url:'index.php?s=/wap/member/updateAddressDefault',
				method:'POST',
				data:{
					id:id
				}
			}).then(res=>{
				this.getAddressList(this.page);
			});
		},
		// 点击跳转到new-address
		newAddress(id) {
			uni.navigateTo({
				url: '/pages/components/new-address/new-address'
			})
		},
		// 点击跳转到edit-address页面
		editAddress(id) {
			uni.navigateTo({
				url:'/pages/components/new-address/new-address?id='+id
			})
		},
		//删除地址
		deleteAddress(id){
			let that = this;
			uni.showModal({
			    title: '提示',
			    content: '确定要删除该地址吗？',
			    success: function (res) {
			        if (res.confirm) {
			            request({
			            	url:'index.php?s=/wap/member/memberAddressDelete',
			            	method:'POST',
			            	data:{
			            		id:id
			            	}
			            }).then(res=>{
			            	that.getAddressList(that.page);
			            });
			        }
			    }
			});
		},
	}
}
